.slide-h {
  height: 100%;
}
.slide-h .background-h {
  position: relative;
  width: 100%;
  height: 100%;
}
.slide-h #print-zone {
  height: 100%;
}
.slide-h .button-wrapper-h {
  position: absolute;
  bottom: 0;
  right: 8px;
  width: 60%;
  height: 65px;
  z-index: 1;
}
.slide-h .button-wrapper-h img {
  width: 43%;
  margin-right: 7%;
  position: relative;
}
.slide-h .hide {
  display: none;
}
.slide-h .show {
  opacity: 0;
}
.slide-h .output-img {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2;
}
.slide-h .qrcode-wrapper {
  position: absolute;
  left: 15px;
  bottom: 15px;
  text-align: center;
}
.slide-h .qrcode-wrapper .qrcode {
  border-radius: 50%;
  width: 60px;
}
.slide-h .qrcode-wrapper p {
  line-height: 1.5;
  color: #fff;
  font-size: 10px;
}
.slide-h .customized-name {
  position: absolute;
  top: 9.5%;
  left: 43%;
  z-index: 1;
  font-size: 22px;
}
.slide-h .customized-name p {
  line-height: 1.5;
  text-align: center;
}
.slide-h .color1 {
  color: #7B5039;
}
.slide-h .color2 {
  color: #7B5039;
}
.slide-h .color3 {
  color: #7C5314;
}
.slide-h .color4 {
  color: #7C5314;
}
.slide-h .color5 {
  color: #3C5534;
}
.slide-h .color6 {
  color: #3C5534;
}
.slide-h .color7 {
  color: #95492B;
}
.slide-h .color8 {
  color: #95492B;
}



