@import "~slick-carousel/slick/slick.css";
@import "~slick-carousel/slick/slick-theme.css";
.page-wrapper {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.order-container {
  position: relative;
  overflow: hidden;
  min-height: 400px;
}
.order-container .form-container {
  width: 1204px;
  min-height: 400px;
  text-align: center;
  padding: 40px 100px 0;
  position: relative;
}
.order-container .form-container .ant-input-number {
  width: 60px;
}
.order-container .form-container .tri-col-wrapper {
  text-align: left;
}
.order-container .form-container .tri-col-wrapper .ant-form-item-label {
  width: 100px;
}
.order-container .form-container .tri-col-wrapper .ant-form-item-control-wrapper {
  width: 150px;
}
.order-container .form-container .bi-col-wrapper {
  text-align: left;
}
.order-container .form-container .bi-col-wrapper .ant-form-item-label {
  width: 180px;
}
.order-container .form-container .bi-col-wrapper .ant-form-item-control-wrapper {
  width: 80px;
}
.order-container .form-container .ant-row.ant-form-item {
  display: inline-block;
}
.order-container .form-container .ant-form-item-control-wrapper {
  display: inline-block;
  margin-right: 20px;
}
.order-container img {
  position: absolute;
  opacity: 0.7;
}



