.wrapper-a {
  width: 100%;
  height: 100%;
  overflow: hidden;
  text-align: center;
}
.wrapper-a .background-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
}
.wrapper-a .background-wrapper img {
  width: 100%;
  height: 100%;
}
.wrapper-a .top-text {
  position: relative;
  width: 80%;
  height: auto;
  margin: 10% auto;
}
.wrapper-a .man-fish {
  position: absolute;
  bottom: 5%;
  height: 75%;
  left: 50%;
  -webkit-transform: translate(-55%);
      -ms-transform: translate(-55%);
          transform: translate(-55%);
}
.wrapper-a .red-packet {
  width: 90%;
  position: absolute;
  left: 5%;
  height: 30%;
}
.wrapper-a .red-packet .red-packet-1 {
  position: absolute;
  width: 50px;
  left: 5%;
  top: 0;
}
.wrapper-a .red-packet .red-packet-2 {
  position: absolute;
  width: 30px;
  right: 10%;
  top: 28%;
}
.wrapper-a .red-packet .red-packet-3 {
  position: absolute;
  width: 30px;
  left: 7%;
  top: 84%;
}
.wrapper-a .red-packet .red-packet-4 {
  position: absolute;
  width: 20px;
  right: 8%;
  top: 68%;
}
.wrapper-a .wave-wrapper {
  width: 100%;
  height: 280px;
  position: absolute;
  bottom: 67px;
}
.wrapper-a .left-wave {
  position: absolute;
  left: 0;
  top: 0;
  width: 30%;
}
.wrapper-a .right-wave {
  position: absolute;
  right: 0;
  top: 14px;
  width: 34%;
}
.wrapper-a .wave1 {
  position: absolute;
  width: 100%;
  bottom: 13%;
  left: 0;
}
.wrapper-a .wave2 {
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
}
.wrapper-a .btm-wave {
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
}
.wrapper-a .submit-btn {
  width: 76%;
  position: absolute;
  bottom: 12%;
  left: 12%;
}



